import '@babel/polyfill'
import 'mutationobserver-shim'
import Vue from 'vue'
import './plugins/bootstrap-vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret, faUserCircle, faLayerGroup, faCompass, faAngleDown, faAngleRight, faSearchPlus, faDownload, faSquare, faCheckSquare } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import axios from 'axios'

library.add(faUserSecret, faUserCircle, faLayerGroup, faCompass, faAngleDown, faAngleRight, faSearchPlus, faDownload, faSquare, faCheckSquare)

// axios API default
axios.defaults.baseURL = "https://yliflood.yunlin.gov.tw/v1/api"
//axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.withCredentials = false;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.config.productionTip = false
Vue.use(BootstrapVue)
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
