<template>
  <div id="main_view">
    <div id="OMap" style="width: 100%;height: 100%;"></div>
    <div id="logo">
      <b-img src="../assets/logo.png" width="350px" height="83px" />
    </div>
    <div id="mymenu" class="rounded shadow">
      <my-menu />
    </div>
    <div id="layer">
      <font-awesome-icon icon="layer-group" size="4x" @click="logout" />
    </div>
    <div id="version">
      <strong>ver. 0.1.1204</strong>
    </div>
  </div>
</template>

<style>
html,
body {
  margin: 0;
  padding: 0;
}

#main_view {
  font-family: "Microsoft JhengHei", "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100vw;
  height: 100vh;
}

#logo {
  position: absolute;
  left: 20px;
  top: 20px;
  max-width: 350px;
  max-height: 100px;
  z-index: 1000;
}

#mymenu {
  position: absolute;
  left: 20px;
  top: 120px;
  width: 30%;
  height: 75%;
  background-color: whitesmoke;
  font-family: Microsoft JhengHei;
  font-weight: bold;
  overflow: overlay;
  opacity: 0.85;
  z-index: 1000;
}

#avatar {
  position: absolute;
  right: 70px;
  top: 100px;
  width: 70px;
  height: 70px;
  z-index: 1000;
}

#layer {
  position: absolute;
  right: 70px;
  top: 180px;
  width: 70px;
  height: 70px;
  z-index: 1000;
}

#version {
  position: absolute;
  right: 20px;
  bottom: 20px;
  width: 100px;
  height: 40px;
  z-index: 1000;
}
</style>
<script>
let messageBox; //訊息視窗物件

let pMap; //初始化地圖物件

//------------------------------須自行修改的參數,包含點位坐標,訊息視窗內容及圖示檔案來源設定------------------------------
var infotext = "<B>雲林縣</B><br>雲林縣"; //地標名稱及訊息視窗內容
var markerPoint = new TGOS.TGPoint(186065.13, 2621248.49); //地標坐標位置
var imgUrl = "http://api.tgos.tw/TGOS_API/images/marker2.png"; //標記點圖示來源
//------------------------------若網頁介面依照範例網頁的預設設定,以下程式碼可不修改---------------------------------------

function InitWnd() {
  //------------------初始化地圖--------------------
  let pOMap = document.getElementById("OMap");
  let mapOptiions = {
    navigationControl: false,
    navigationControlOptions: {
      controlPosition: TGOS.TGControlPosition.LEFT_BOTTOM, //控制項位置
      navigationControlStyle: TGOS.TGNavigationControlStyle.SMALL //控制項樣式
    },
    scaleControl: true
  };

  pMap = new TGOS.TGOnlineMap(pOMap, TGOS.TGCoordSys.EPSG3826, mapOptiions); //建立地圖,選擇TWD97坐標

  pMap.setZoom(7); //初始地圖縮放層級
  pMap.setCenter(markerPoint); //初始地圖中心點
  //------------------建立標記點---------------------

  var markerImg = new TGOS.TGImage(
    imgUrl,
    new TGOS.TGSize(38, 33),
    new TGOS.TGPoint(0, 0),
    new TGOS.TGPoint(10, 33)
  ); //設定標記點圖片及尺寸大小

  var pTGMarker = new TGOS.TGMarker(pMap, markerPoint, "", markerImg); //建立機關單位標記點

  //-----------------建立訊息視窗--------------------

  var InfoWindowOptions = {
    maxWidth: 4000, //訊息視窗的最大寬度

    pixelOffset: new TGOS.TGSize(5, -30), //InfoWindow起始位置的偏移量, 使用TGSize設定, 向右X為正, 向上Y為負

    zIndex: 99 //視窗堆疊順序
  };

  messageBox = new TGOS.TGInfoWindow(infotext, markerPoint, InfoWindowOptions); //建立訊息視窗

  TGOS.TGEvent.addListener(pTGMarker, "mouseover", openInfoWindow); //滑鼠監聽事件--開啟訊息視窗

  TGOS.TGEvent.addListener(pTGMarker, "mouseout", closeInfoWindow); //滑鼠監聽事件--關閉訊息視窗
}

function openInfoWindow() {
  //開啟訊息視窗函式
  messageBox.open(pMap);
}

function closeInfoWindow() {
  //關閉訊息視窗函式
  messageBox.close();
}

import qs from "qs";
import MyMenu from "@/views/MyMenu";
import axios from "axios";
import { TokenRet } from "../api/types";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "mainView",
  components: {
    MyMenu
  },
  mounted() {
    InitWnd();
  },
  methods: {}
};
</script>