<template>
  <div id="app">
    <main-view v-if="isLogin"></main-view>
    <login v-else></login>
  </div>
</template>

<style>
html,
body {
  margin: 0;
  padding: 0;
}

#app {
  font-family: "Microsoft JhengHei", "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background-color: burlywood;
  width: 100vw;
  height: 100vh;
}
</style>
<script>
import qs from "qs";
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
import Login from "./views/Login";
import MainView from "./views/MainView";
export default {
  name: "app",
  components: {
    Login,
    MainView
  },
  computed: {
    ...mapGetters(["isLogin"])
  },
  methods: {
    login() {
      let param = {
        grant_type: "password",
        username: "yunlindemo",
        password: "Demo1234"
      };

      axios
        .post("/token", qs.stringify(param))
        .then(res => {
          const ret = res.data;
          if (res.status === 200) {
            this.setToken(ret.access_token);
          } else {
            alert("Login failed!");
          }
        })
        .catch(err => alert(err));
    }
  }
};
</script>