import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '@/views/Login.vue'

Vue.use(VueRouter)

const LOGIN_PAGE_NAME = 'login'

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: `/${LOGIN_PAGE_NAME}`,
    name: LOGIN_PAGE_NAME,
    component: Login
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/cwb',
    name: 'cwb',
    component: () => import('../views/EmbedSite.vue'),
    props: { url: 'https://www.cwb.gov.tw' }
  },
  {
    path: '/ncdr',
    name: 'ncdr',
    component: () => import('../views/EmbedSite.vue'),
    props: { url: 'https://watch.ncdr.nat.gov.tw/watch_typhoon.aspx' }
  },
  {
    path: '/rain_alert',
    name: 'rain_alert',
    component: () => import('../views/RainAlertPage.vue'),
  },
  {
    path: '/rain_report',
    name: 'rain_report',
    component: () => import('../views/RainReportPage.vue'),
  },
  {
    path: '/rain_forecast/:period',
    name: 'rain_forecast',
    component: () => import('../views/RainForecastPage.vue'),
  },
  {
    path: '/rain_accumulation/:period',
    component: () => import('../views/RainAccumulationPage.vue'),
  },
  {
    path: '/water_level_alert',
    name: 'water_level_alert',
    component: () => import('../views/WaterLevelAlertPage.vue'),
  },
  {
    path: '/water_level_report',
    name: 'water_level_report',
    component: () => import('../views/WaterLevelReportPage.vue')
  },
  {
    path: '/water_level_detail/:b2_id',
    name: 'water_level_detail',
    component: () => import('../views/WaterLevelDetailPage.vue'),
    props: true
  },
  {
    path: '/waterLevel/:river/:site',
    name: 'waterLevel',
    component: () => import('../views/WaterLevelPage.vue'),
  },
  {
    path: '/flood_alert',
    name: 'flood_alert',
    component: () => import('../views/FloodAlertPage.vue'),
  },
  {
    path: '/cctv_alert',
    name: 'cctv_alert',
    component: () => import('../views/CctvAlertPage.vue'),
  },
  {
    path: '/yl_pump_system',
    name: 'yl_pump_system',
    component: () => import('../views/EmbedSite.vue'),
    props: { url: 'http://61.216.39.38:99/Default.aspx' }
  },
  {
    path: '/pump',
    name: 'pump',
    component: () => import('../views/PumpPage.vue'),
  },
  {
    path: '/gate',
    name: 'gate',
    component: () => import('../views/GatePage.vue'),
  },
  {
    path: '/pond',
    name: 'pond',
    component: () => import('../views/PondPage.vue'),
  },
  {
    path: '/pump_list',
    name: 'pump_list',
    component: () => import('../views/PumpListPage.vue'),
  },
  {
    path: '/parts',
    name: 'parts',
    component: () => import('../views/PartsPage.vue'),
  },
  {
    path: '/decisionReport',
    name: 'decisionReport',
    component: () => import('../views/DecisionReport.vue'),
  },
  //PumpLogistics
  {
    path: '/PumpLogistics',
    name: 'PumpLogistics',
    component: () => import('../views/PumpLogistics.vue'),
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
