<template>
  <b-list-group-item :button="true">
    <div v-if="isFolder" @click.stop="toggle">
      <font-awesome-icon v-if="!isOpen" icon="angle-right" size="lg" />
      <font-awesome-icon v-if="isOpen" icon="angle-down" size="lg" />
      {{ item.title }}
      <font-awesome-icon
        v-if="item.download_url"
        icon="download"
        size="lg"
        @click.stop="openLink(item.download_url)"
        class="float-right border"
      />
      <font-awesome-icon
        v-if="item.detail_name"
        icon="search-plus"
        size="lg"
        @click.stop="goDetail(item.title, item.detail_name, item.detail_params)"
        class="float-right border"
      />
    </div>
    <div v-else @click.stop="goLink">
      <font-awesome-icon v-if="item.state === true" icon="check-square" size="lg" />
      <font-awesome-icon v-if="item.state === false" far icon="square" size="lg" />
      {{ item.title }}
      <font-awesome-icon
        v-if="item.download_url"
        icon="download"
        size="lg"
        @click.stop="openLink(item.download_url)"
        class="float-right border"
      />
      <font-awesome-icon
        v-if="item.detail_url"
        icon="search-plus"
        size="lg"
        @click.stop="goDetail(item.name, item.detail_url)"
        class="float-right border"
      />
    </div>

    <b-list-group v-show="isOpen" v-if="isFolder">
      <menu-list
        class="item"
        v-for="(child, index) in item.children"
        :key="index"
        :item="child"
        @showPage="passEvt"
      ></menu-list>
    </b-list-group>
  </b-list-group-item>
</template>
<style scoped>
ul {
  text-align: left;
}

.item {
  cursor: pointer;
}
</style>
<script>
export default {
  name: "MenuList",
  props: {
    item: Object,
    topLevel: Boolean
  },
  data() {
    return {
      isOpen: false
    };
  },
  computed: {
    isFolder: function() {
      return this.item.children && this.item.children.length;
    }
  },
  methods: {
    toggle: function() {
      if (this.isFolder) {
        this.isOpen = !this.isOpen;
      }
    },
    goLink() {
      if (this.item) {
        if (this.item.state !== undefined) {
          this.item.state = !this.item.state;
          if (this.item.state && this.item.msg) alert(this.item.msg);
        }

        if (this.item.name) {
          this.$emit("showPage", {
            title: this.item.title,
            name: this.item.name,
            params: this.item.params
          });
        } else if (this.item.url) {
          this.$emit("showPage", {
            title: this.item.title,
            url: this.item.url
          });
        } else if (this.item.extern_url) {
          window.open(this.item.extern_url);
        }
      }
    },
    goDetail(title, name, params) {
      this.$emit("showPage", { title, name, params });
    },
    passEvt(arg) {
      this.$emit("showPage", arg);
    },
    openLink(url) {
      window.open(window.location.origin + "/" + url);
    }
  }
};
</script>